import Strings from './Strings'
import Canvas from './Canvas'
import { useEffect } from 'react'

function Fretboard(props) {

    const { draw, setHeight, fretHeight, stringNotesArray, height, preSpacing, showNote, setLastNote, useSharps } = props

    return (
        <div className='fretboardContainer'>
            <Canvas draw={draw} options={{ context: '2d' }} />
            <Strings fretHeight={fretHeight} stringNotesArray={stringNotesArray} height={height} fretSpacing={preSpacing} showNote={showNote} setLastNote={setLastNote} useSharps={useSharps} />
        </div>
    )
}

export default Fretboard