import { useState, useRef, useEffect } from 'react'
import './App.css'
import String from './String'
import { Midi } from "@tonaljs/tonal"
import Fretboard from './Fretboard'
import StringSelection from './StringSelection'
import Settings from './Settings'
import LastPlayed from './LastPlayed'

const FRET_RATIO = 17.817
const MIDI_NOTES = []


for (let i=0; i < 128; i++) {
  MIDI_NOTES.push({
    "value": i,
    "label": Midi.midiToNoteName(i)
  })
}


function App() {
  const [frets, setFrets] = useState(16)
  //guitar: [64, 59, 55, 50, 45, 40]
  //bass: [43, 38, 33, 28]
  console.log(MIDI_NOTES)
  const [stringNotesArray, setStringNotesArray] = useState([64, 59, 55, 50, 45, 40])
  const preSpacing = []
  const [appHeight, setAppHeight] = useState(0)
  const [appWidth, setAppWidth] = useState(0)
  const strings = stringNotesArray.length
  const fretHeight = appHeight / strings
  let fretSpacing = []
  const postHeight = 7
  const appRef = useRef(null)
  const [showNote, setShowNote] = useState(true)
  const [lastNote, setLastNote] = useState('--')
  const [useSharps, setUseSharps] = useState(false)

  const customStyles = {
    control: base => ({
      ...base,
      height: '100%',
      minHeight: 5
    })
  }

  useEffect(() => {
    const current = appRef.current.getBoundingClientRect()
    setAppHeight(current.height)
    console.log('height: ' + appHeight)
    setAppWidth(current.width)
    console.log('width: ' + appWidth)

  })
  let curr = 0
  let prev = curr
  let totalFrets = 0

  for (let i = 0; i < frets; i++) {
    prev = curr
    curr += (25.5 - prev) / 17.817

    preSpacing.push(curr - prev)
    totalFrets += preSpacing[preSpacing.length - 1]
  }

  preSpacing.unshift(1)
  totalFrets += 1

  console.log('width: ' + appWidth)
  console.log('height: ' + appHeight)
  
  const draw = (ctx) => {
    const width = ctx.canvas.width
    const height = ctx.canvas.height
    console.log('width: ' + width)
    console.log('height: ' + height)

    const widthRatio = width / totalFrets
    let canvasSpacing = preSpacing.map(fretDist => fretDist * widthRatio)
    let fretDist = 0
    let prev = 0
    ctx.fillStyle = 'rgb(135, 62, 35)'
    ctx.fillRect(0, 0, width, height)
    ctx.strokeStyle = 'rgb(234, 244, 200)'
    ctx.fillStyle = 'rgb(255, 255, 255)'
    ctx.lineWidth = width / 300



    //drawing nut
    fretDist += canvasSpacing[0]
    ctx.beginPath()
    ctx.moveTo(fretDist, 0)
    ctx.lineTo(fretDist, height)
    ctx.stroke()

    ctx.strokeStyle = 'rgb(108, 122, 134)'
    ctx.lineWidth = width / 500

    for (let i = 1; i < canvasSpacing.length; i++) {
      fretDist += canvasSpacing[i]

      //drawing frets
      ctx.beginPath()
      ctx.moveTo(fretDist, 0)
      ctx.lineTo(fretDist, height)
      ctx.stroke()



      //drawing circles
      if (i % 12 == 3 || i % 12 == 5 || i % 12 == 7 || i % 12 == 9) {
        ctx.moveTo(fretDist - (canvasSpacing[i] / 2), height / 2)
        ctx.arc(fretDist - (canvasSpacing[i] / 2), height / 2, 12, 0, 2 * Math.PI, false)
        ctx.fill()
      } else if (i % 12 == 0 && i != 0) {
        ctx.moveTo(fretDist - (canvasSpacing[i] / 2), height / 4)
        ctx.arc(fretDist - (canvasSpacing[i] / 2), height / 4, 11, 0, 2 * Math.PI, false)
        ctx.fill()
        ctx.moveTo(fretDist - (canvasSpacing[i] / 2), (height * 3) / 4)
        ctx.arc(fretDist - (canvasSpacing[i] / 2), (height * 3) / 4, 11, 0, 2 * Math.PI, false)
        ctx.fill()
      }

    }

    ctx.fillStyle = 'rgb(108, 122, 134)'

    //drawing strings and tuning posts
    for (let j = 0; j < strings; j++) {
      const currentHeight = (fretHeight * j) + (fretHeight / 2)
      ctx.lineWidth = (height / 250) * (j + 1)

      ctx.beginPath()
      ctx.moveTo(canvasSpacing[0] / 3, currentHeight)
      ctx.arc(canvasSpacing[0] / 3, currentHeight + postHeight, postHeight, 0, 2 * Math.PI, false)
      ctx.fill()
      ctx.moveTo(canvasSpacing[0] / 3, currentHeight)
      ctx.strokeStyle = 'rgb(192,192,192)'
      ctx.lineTo(width, currentHeight)
      ctx.stroke()
    }

  }

  return (
    <div className='topApp'>
      <LastPlayed lastNote={lastNote} />
      <div className='App' ref={appRef}>
      <StringSelection MIDI_NOTES={MIDI_NOTES} customStyles={customStyles} stringNotesArray={stringNotesArray} Midi={Midi} strings={strings} setStringNotesArray={setStringNotesArray} />
      <Fretboard draw={draw} fretHeight={fretHeight} stringNotesArray={stringNotesArray} height={appHeight} preSpacing={preSpacing} showNote={showNote} setLastNote={setLastNote} useSharps={useSharps} />
      </div>
      <Settings setShowNote={setShowNote} showNote={showNote} frets={frets} setFrets={setFrets} useSharps={useSharps} setUseSharps={setUseSharps} />
    </div>
  )
}

export default App;
