import Checkbox from './Checkbox'
import NumberInput from './NumberInput'

function Settings(props) {
    const { setShowNote, showNote, frets, setFrets, useSharps, setUseSharps } = props

    return (
        <div className='settings'>
        <h2 className='settingsHeading'>Settings</h2>
        <Checkbox value={showNote} settingsMessage='Show notes on hover: ' setState={setShowNote} name='showNote' />
        <Checkbox value={useSharps} settingsMessage='Use sharps: ' setState={setUseSharps} name='useSharps' />
        <NumberInput settingsMessage='Number of frets shown: ' name='numFrets' value={frets} setState={setFrets} />
        </div>
    )
}

export default Settings