import Select from 'react-select'
import { useEffect, useState } from 'react'

function StringSelect(props) {

    const { i, MIDI_NOTES, customStyles, stringNotesArray, Midi, setStringNotesArray } = props
    const [value, setValue] = useState({ value: stringNotesArray[i], label: Midi.midiToNoteName(stringNotesArray[i]) })

    useEffect(() => {
        setValue({ value: stringNotesArray[i], label: Midi.midiToNoteName(stringNotesArray[i]) })
    }, [stringNotesArray])

    const handleChange = event => {
        setValue(event)
        let copy_stringNotesArray = [...stringNotesArray]
        copy_stringNotesArray[i] = event.value
        setStringNotesArray(copy_stringNotesArray)
    }

    const handleRemove = event => {
        if (stringNotesArray.length > 1) {
            let copy_stringNotesArray = [...stringNotesArray]
            copy_stringNotesArray.splice(i, 1)
            setStringNotesArray(copy_stringNotesArray)
        }
    }

    const handleAdd = event => {
        let copy_stringNotesArray = [...stringNotesArray]
        copy_stringNotesArray.splice(i, 0, stringNotesArray[i])
        setStringNotesArray(copy_stringNotesArray)
    }


    return (
        <li key={i} className='listSelect'><button className="removeString" onClick={handleRemove}>- Remove</button><button class="addString" onClick={handleAdd}>+ Add Below</button><Select options={MIDI_NOTES} className='select' styles={customStyles} value={value} onChange={handleChange} /></li>
    )
}

export default StringSelect