
function Checkbox(props) {

    const {settingsMessage, name, value, setState} = props

    const onChange = event => {
        const target = event.target
        const newValue = target.checked
        setState(newValue)
    }
    return (
        <div>
        <label for={name}>{settingsMessage}</label><input name={name} type='checkbox' checked={value} onChange={onChange}></input>
        </div>
    )
}

export default Checkbox