import { useEffect, useRef } from "react"
import String from './String'
import * as Tone from 'tone'

const Strings = props => {

  const sampler = new Tone.Sampler({
    urls: {
      "A1": "A1.mp3",
      "A2": "A2.mp3",
      "A4": "A4.mp3",
      "B3": "B3.mp3",
      "D2": "D2.mp3",
      "D3": "D3.mp3",
      "D5": "D5.mp3",
      "E1": "E1.mp3",
      "E2": "E2.mp3",
      "E4": "E4.mp3",
      "G2": "G2.mp3",
      "G3": "G3.mp3",
    },
    release: 1,
    baseUrl: "./notes/"
  }).toDestination();
  const vol = new Tone.Volume(20).toDestination()

  const { fretHeight, fretSpacing, stringNotesArray, height, showNote, setLastNote, useSharps } = props
  const stringsArray = stringNotesArray.map((string) =>
    <String key={stringNotesArray.indexOf(string)} stringNote={string} fretSpacing={fretSpacing} height={fretHeight} synth={sampler.connect(vol)} Tone={Tone} showNote={showNote} setLastNote={setLastNote} useSharps={useSharps} />
  )

  useEffect(() => {
    console.log('yoooo')
  })

  return (
    <ul className='strings' style={{ height: height, top: -height }}>{stringsArray}</ul>
  )
}

export default Strings