
function NumberInput(props) {

    const { settingsMessage, name, value, setState } = props

    const onChange = event => {
        const target = event.target
        const newValue = target.value
        setState(newValue)
    }

    return (
        <div>
        <label for={name}>{settingsMessage}</label><input type='number' value={value} onChange={onChange} min='1' max='30'></input>
        </div>
    )

}

export default NumberInput