import StringSelect from './StringSelect'

function StringSelection(props) {

    const { MIDI_NOTES, customStyles, stringNotesArray, Midi, strings, setStringNotesArray } = props

    const stringSelection = []

    for (let i = 0; i < strings; i++) {
        console.log(i)
        console.log(stringNotesArray[i])
        stringSelection.push(<StringSelect  i={i} key={i} MIDI_NOTES={MIDI_NOTES} customStyles={customStyles} stringNotesArray={stringNotesArray} Midi={Midi} setStringNotesArray={setStringNotesArray} />)
    }
    return (
        <ul className='stringSelection'>
            {stringSelection}
        </ul>
    )
}

export default StringSelection