import Canvas from "./Canvas";
import { Note } from '@tonaljs/tonal'

const Fret = props => {

    const { synth, note, Tone, showNote, setLastNote, useSharps } = props
    let curNote = Note.pitchClass(note)
    if (useSharps) {
        curNote = Note.enharmonic(curNote)
    }

    function handleMouseDown() {
        synth.triggerAttackRelease(note, 2);
        setLastNote(Note.pitchClass(curNote))
    }

    let noteShown = ""

    if (showNote) {
        noteShown = curNote
    }

    return (
        <button onMouseDown={handleMouseDown}><p>{noteShown}</p></button>   
    )
}

export default Fret